.list{
    background-color: darkcyan;
    color: white;
    width: 345px;
    border: 0;
    height: 50px;
    margin: 20px auto;
    padding: 0px 20px;
    border-radius: 10px;
    font-size: 28px;
}

.list p{
    padding: 8px;
    position: relative;
    text-align: left;
}
.list p span{
    position: absolute;
    right: 8px;
}

.faicons{
    height: 32px;
}