#to-do-form input{
    background-color: brown;
    width: 260px;
    border: 0;
    height: 50px;
    margin: 20px;
    padding: 0px 20px;
    border-radius: 10px;
    font-size: 28px;
    outline: none;
    color:white;
}

#to-do-form ::placeholder{
    color: rgb(233, 227, 219);
    opacity: 70%;
}

#to-do-form button{
    height: 50px;
    border-radius: 10px;
    width: 80px;
    margin: 15px 0px;
    border: 0;
    padding: 0px 20px;
    font-size: 25px;
    text-align: center;
    outline: none;
}